* {
    margin: 0;
    box-sizing: border-box;

}

/*inicio*/
.header {
    background-color: rgb(247, 241, 233);
    font-family: 'Aboreto', cursive;
    background-size: cover;
    height: 80px;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;

}

/*navegaci√≥n*/
.nav {
    display: flex;
    justify-content: space-between;
    max-width: 992x;
    margin: 0 auto;
    cursor: pointer;
}

.nav-link {
    color: black;
    text-decoration: none;
}

.logo {

    font-size: 30px;
    font-weight: bold;
    padding: 10 40px;
    line-height: 80px;

}

.titulo {
    color: azure;
    position: absolute;
    top: 20%;
    width: 50%;
    text-align: center;
    margin: 0 auto;


}

.nav-menu {
    display: flex;
    margin-right: 40px;
    list-style: none;
}

.nav-menu-item {
    font-size: 15px;
    margin: 0 10px;
    line-height: 80px;
    text-transform: uppercase;
    width: max-content;
}

.nav-menu-link {
    padding: 8px 12px;
    border-radius: 3px;
}

.nav-menu-link:hover {
    background-color: white;
    transition: 0.5s;
}

/*icono*/
.nav-toggle {
    color: white;
    background: none;
    border: none;
    font-size: 30px;
    padding: 0 20px;
    line-height: 60px;
    cursor: pointer;
    display: none;
}

/*principal*/
.contenedor {
    color: gray;
    font-family: 'Moon Dance', cursive;
    width: 100%;

    margin: 0 auto;
    height: 400px;
    overflow: hidden;
    margin: 10px 10px;
    box-shadow: 5px 8px 12px black;

}

/*imagenes inicio*/
.pri {
    width: 100%;

    box-shadow: 5px 8px 12px black;
}

.seg {
    width: 100%;
    height: 390px;
    box-shadow: 5px 8px 12px black;
}

body {
    background-color: rgb(240, 229, 214);
    padding: 90px 15px;
    margin: 20px;
}

/*imagenes inicio*/
.imagen1 {

    width: 100%;
    height: 200px;
    box-shadow: black;
}

.imagen2 {
    width: 100%;
    height: 350px;
}

/*texto*/
#texinicio {
    background-color: rgb(240, 229, 214);
    color: black;
    text-align: center;
    font-family: 'Klee One', cursive;
    padding: 10px;
    margin: 10px;

}



/*imagenes centro*/

#inicio {
    display: grid;
    gap: 5px;
    background-size: contain;
    box-sizing: border-box;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 200px 150px;
    grid-template-areas:
        " img1 img2 img3 img4"
        "texto texto texto img4"
    ;

}

div:nth-child(1) {
    grid-area: img1;
}

div:nth-child(2) {
    grid-area: img2;
}

div:nth-child(3) {
    grid-area: img3;
}

div:nth-child(4) {
    grid-area: img4;
}

div:nth-child(5) {
    grid-area: texto;
}





/*pie*/
footer {
    background-color: rgb(240, 229, 214);
    display: flex;
    justify-content: space-between;
    text-align: center;
    padding: 10px;
}

footer ul {
    background-image: image();
    list-style: none;
    display: flex;
    gap: 5px;
}

/*icono pie*/

.sol {
    margin: auto;
    display: block;
    
}

/*Bodega*/

#bodega {


    width: 100%;
    margin: auto;

    .textbodega {

        color: rgb(163, 161, 161);
        font-family: 'Klee One', cursive;
        text-align: center;

    }

}

h3 {
    text-align: center;
    color: rgb(74, 72, 72);
    font-family: 'Klee One', cursive;

}

#imgbodega {
    display: flex;
    gap: 5px;
    margin: 5px;
    padding: 5px;
    background-size: contain;

    .bodega {
        max-width: 50%;

    }
}

/*nuestros vinos*/

#nuestrosvinos {
    display: flex;

    justify-content: center;
    gap: 10px;
    #nuestrosvinos div {
        flex-grow: 1;
        text-align: center;



    }

    .imagen3 {

        padding: 10%;
        width: 200px;
        height: 200px;

       


    }

    h3 {
        color: rgb(160, 152, 152);
        font-family: 'Klee One', cursive;
        text-align: center;
    }
}



/*contacto*/
#contacto {
    display: flex;

    #imagencontacto {
        padding: 0 10px;
        background-size: contain;

        .imagcontac {
            width: 600px;
            height: 710px;
        }
    }
}

form {
    width: 500px;
    margin: auto;
    background: none;
    padding: 10 10px;
    box-sizing: border-box;
    margin-top: 5px;
    border-radius: 7px;
    font-family: 'Klee One', cursive;
    color: rgb(81, 79, 79);
    font-size: 15px;

    .consultas {
        font-display: column;
        width: 10%;
    }

    Input,
    textarea {
        width: 100%;
        margin-bottom: 20px;
        box-sizing: border-box;
    }

    h2 {
        color: #fff;
        text-align: center;
        margin: 0;
        font-size: 30px;
        margin-bottom: 10px;
    }

    .mapa {
        width: 600px;
        height: 200px;
    }
}


/*Nosotros*/
#nosotros {
    display: flex;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;



}

#nosotros .imagen2 {
    display: flex;
    width: 300px;
    height: 200px;
    margin-bottom: 15px;

}

#principalnosotros {
    color: rgb(103, 101, 101);
    font-family: 'Klee One', cursive;
    text-align: center;
}

/*turismo*/
#turismo {
    display: flex;
    justify-content: center;
    gap: 10px;
    padding: 20px;
    div {
        flex-grow: 1;
        text-align: center;
        padding:  20px 30px;
        img{
            width: 300px;
            height: 300px;

        }
    }
    p{
        color: rgb(103, 101, 101);
        font-family: 'Klee One', cursive;
        text-align: center;
    }

}

#turismo2 {
    width: 100%;
    margin: 0 auto;
    height: 400px;
    overflow: hidden;
    margin: 10px 10px;
    box-shadow: 4px 8px 12px black;
}

